<template>
  <chart-card>
    <template>
      <div style="height: 100%">
        <div :id="refName" :ref="refName" v-loading="loading" style="height: 340px;" />
      </div>
    </template>
  </chart-card>
</template>

<script>
import '@/plugins/echarts/echartsTheme.js'
import { zoom } from '@/mixins/echarts'
import ChartCard from '@/views/analysis/ChartCard'
import { getAgingListApi } from '@/apis/modules/analysis/overview'

export default {
  components: {
    ChartCard
  },
  mixins: [zoom],
  data() {
    return {
      refName: 'StatisticLine',
      options: {}
    }
  },
  mounted() {
    this.screenWidth = window.screen.width || document.documentElement.clientWidth
    this.screenHeight = window.screen.height || document.documentElement.clientHeight
    if (this.screenWidth && (this.screenWidth === 1024 || this.screenWidth < 1024)) {
      this.zoom = 0.8
    }
    this.resize()
    this.init()
    this.resize()
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getAgingListApi()
          .then(res => {
            const { code, data } = res
            if (!code) {
              const colors = ['#2ec7c9', '#b6a2de', '#5ab1ef', '#ffb980']
              const { xAxis } = data
              const legendData = data.series.map(c => c.name)
              const series = data.series.map((c, i) => ({ ...c, color: colors[i], smooth: true }))
              this.getCompleteOptions(legendData, series, xAxis)
            }
            this.loading = false
            resolve(res)
          })
          .catch(err => {
            this.loading = false
            reject(err)
          })
      })
    },
    getCompleteOptions(legendData, series, xAxis) {
      this.options = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          }
        },
        legend: {
          data: legendData,
          left: 0
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        yAxis: [
          {
            type: 'value',
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: xAxis
        },
        series
      }
      const chart = this.$refs[this.refName]
      this.chart = chart && this.$echarts.init(chart, 'macarons')
      chart && this.chart.setOption(this.options)
      this.chart && this.chart.resize()
    }
  }
}
</script>

