import request from '@/apis/request.wms'

// 库存总额及平均库存
export function getInventoryAmountChartApi(data = {}) {
  return request({
    url: '/statistics/wmsBillboardsChart/inventoryAmountChart',
    method: 'post',
    data
  })
}

