<template>
  <el-row :gutter="20" style="height: 100%">
    <el-col v-for="(item, index) in list" :key="index" :span="6">
      <el-statistic group-separator="," :value="item.value">
        <template slot="prefix">
          <i class="el-icon-s-order" :style="{color: item.color,fontSize: '20px'}" />
        </template>
        <template slot="title">
          <span class="statistic-title">{{ item.name }}</span>
        </template>
      </el-statistic>
    </el-col>
  </el-row>
</template>

<script>
import { getInventoryAgePieChartApi } from '@/apis/modules/panel/zhongyuan'
export default {
  data() {
    return {
      list: []
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      return new Promise((resolve, reject) => {
        getInventoryAgePieChartApi()
          .then(res => {
            const { code, data } = res
            if (!code) {
              console.log('data: ', data)
              const colors = ['#2ec7c9', '#b6a2de', '#5ab1ef', '#ffb980']
              this.list = data.map((c, i) => ({ ...c, color: colors[i] }))
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    }
  }
}
</script>

<style lang='scss' scoped>
.statistic-title {
  font-size: 16px;
  font-weight: 600;
  color: #008acd;
}
</style>
