<template>
  <chart-card>
    <template>
      <div style="height: 100%">
        <div :id="refName" :ref="refName" v-loading="loading" style="height: 190px;" />
      </div>
    </template>
  </chart-card>
</template>

<script>
import '@/plugins/echarts/echartsTheme.js'
import ChartCard from '@/views/analysis/ChartCard'
import { zoom } from '@/mixins/echarts'
import { getInventoryProportionListApi } from '@/apis/modules/analysis/overview'

export default {
  name: 'InventoryCom',
  components: {
    ChartCard
  },
  mixins: [zoom],
  data() {
    return {
      refName: 'Inventory',
      options: {}
    }
  },
  mounted() {
    this.screenWidth = window.screen.width || document.documentElement.clientWidth
    this.screenHeight = window.screen.height || document.documentElement.clientHeight
    if (this.screenWidth && (this.screenWidth === 1024 || this.screenWidth < 1024)) {
      this.zoom = 0.8
    }
    this.resize()
    this.init()
    this.resize()
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getInventoryProportionListApi()
          .then(res => {
            const { code, data } = res
            if (!code) {
              const series = [
                {
                  type: 'pie',
                  // center: ['53%', '55%'], // 饼图距离容器上下左右边界的调整使用grid调整无效,第一个数值表示左右位置，第二个数值表示上下位置
                  radius: ['40%', '70%'],
                  // 是否启用防止标签重叠策略，默认开启，在标签拥挤重叠的情况下会挪动各个标签的位置，防止标签间的重叠。
                  // 如果不需要开启该策略，例如圆环图这个例子中需要强制所有标签放在中心位置，可以将该值设为 false。
                  // avoidLabelOverlap: false,
                  // 图形样式
                  itemStyle: {
                    borderRadius: 10,
                    borderColor: '#fff',
                    borderWidth: 3
                  },
                  data: data,
                  // 高亮状态的扇区和标签样式。
                  emphasis: {
                    label: {
                      show: true,
                      fontSize: 16
                    }
                  }
                }
              ]
              this.getCompleteOptions(null, series, null)
            }
            this.loading = false
            resolve(res)
          })
          .catch(err => {
            this.loading = false
            reject(err)
          })
      })
    },
    getCompleteOptions(legendData, series, xAxis) {
      this.options = {
        title: {
          text: '库存占比',
          left: 0,
          top: 0,
          textStyle: {
            fontSize: '18px',
            fontWeight: 800
          }
        },
        tooltip: {
          trigger: 'item',
          formatter: '{b} : {c} ({d}%)'
        },
        series
      }
      const chart = this.$refs[this.refName]
      this.chart = chart && this.$echarts.init(chart, 'macarons')
      chart && this.chart.setOption(this.options)
      this.chart && this.chart.resize()
    }
  }
}
</script>

<style lang='scss' scoped>

</style>
